body{
  background: #ddd;
}
#root{
  display: flex;
  justify-content: center;
  min-height: 100vh;
}

.App {
  color: #333;
  border: 1px solid #aaa;
  border-radius: 4px;
  background: white;
  margin: 2em;
  padding: 0 1em;
  min-height: 20em;
  min-width: 32em;
  box-shadow: 0 0 1em hsla(0,0%,0%,0.15);
}
.App > .todoList> li:nth-child(odd){
  background: #eee;
}
.App > .todoList> li:nth-child(even){
  background: transparent;
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  *::after{
    box-sizing: border-box;
  }
  *::before{
    box-sizing: border-box;
  }
  ul,ol,dl{
    list-style:none;
  }
  a{
    color: inherit;
  }
.TodoInput{
    width: 100%;
    line-height: 1.8;
    margin-bottom: 1em;
    font-size: 16px;
    padding: 0 .2em;
  }
.TodoItem{
    padding: .5em;
    display: flex;
    align-items: center;
  }
  .TodoItem>.title{
    flex-grow: 1;
    padding: 0 .5em;
  }
